import { configureStore, createSlice } from '@reduxjs/toolkit'


let mysr = createSlice({
    name : 'mysr',
    initialState : ['  ', 83, 41, 19, 22, 1]
})
let mywork = createSlice({
    name : 'mywork',
    initialState : ['  ', 120, 37, 11, 60, 2]
})
let soonend = createSlice({
    name : 'soonend',
    initialState : ['  ', 54, 31, 13, 7, 3]
})
let hurrysr = createSlice({
    name : 'hurrysr ',
    initialState : [' ', 46, 22, 13, 9, 2]
})

export default configureStore({
  reducer: {
    mysr : mysr.reducer,
    mywork : mywork.reducer,
    soonend : soonend.reducer,
    hurrysr : hurrysr.reducer
   }
}) 